  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: yellow;
  -webkit-clip-path: circle(100px at 80% 20%);
          clip-path: circle(100px at 80% 20%);
  -webkit-clip-path: circle(100px at 20% 80%);
          clip-path: circle(100px at 20% 80%);
  transition: 0.5s ease-in-out;
}
